import React,{useState,useContext} from 'react'
import axios from 'axios';
import {store} from './App';
import { Redirect } from 'react-router';
import './App.css'

const Login = () => {
    const [token,setToken] = useContext(store);
    const[errormsg,setErrormsg]=useState(null);
    const [data,setData] = useState({
        email:'',
        password:'',
    })
    const changeHandler = e =>{
        setData({...data,[e.target.name]:e.target.value})
    }
    const submitHandler = (e) =>{
        e.preventDefault();
        axios.post("https://authentication-app-pkpr.onrender.com/login",data).then(
            res => setToken(res.data.token)
        ).catch(error=>{
            if(error.response){
                setErrormsg(error.response.data.message)
            }
            
        })
      
    }

    if(token){
       return <Redirect to='/myprofile' />
    }
   
    
    return (
        <div>
            <center>
            <form onSubmit={submitHandler}>
                <h3>Login</h3>
                <input type="email" onChange={changeHandler} name="email" placeholder="Email" autoComplete='true' required/><br />
                <input type="password" onChange={changeHandler} name="password" placeholder="Password" autoComplete='true' required/><br />
                <input type="submit" value="Login" /><br />
                <div>
                    {errormsg ? <div style={{color:"red"}}>{errormsg}</div> : null}
                </div>
                New user ? <a href='/register'>Register</a>
            </form>
            </center>
        </div>
    )
}

export default Login
