import React,{useContext} from 'react';
import { store } from './App';
import './App.css'
import user from './user.png'
const Nav = () => {
    const [token] = useContext(store)
    return (
        <div>
          {!token ?
            <center className='head'>Home <a href='/login' className='userbut'><img src={user} className='userpng'/></a> </center>
            :
            <center className='head'>Dashboard</center>
          }
        </div>    

    )
}

export default Nav
