import React,{useState} from 'react'
import axios from 'axios';
import './App.css'

const Register = () => {
    const[errormsg,setErrormsg]=useState(null)
    const [data,setData] = useState({
        username:'',
        email:'',
        password:'',
        confirmpassword:''
    })
    const changeHandler = e =>{
        setData({...data,[e.target.name]:e.target.value})
    }
    
    const submitHandler = e =>{
        e.preventDefault();
        axios.post("https://authentication-app-pkpr.onrender.com/register",data).then(
            res =>
             {setData({
                username:'',
                email:'',
                password:'',
                confirmpassword:''
            })
            setErrormsg(res.data.message)
            if(res.data.message){
                e.target.reset()
            }
        }).catch(error=>{
                    if(error.response){
                        setErrormsg(error.response.data.message)
                    }
         })
            
           
        
        
         }
           
        
            

    return (
        <div>
            <center>
            <form onSubmit={submitHandler} id="form">
                <h3>Register</h3>
                <input type="text" onChange={changeHandler} name="username" placeholder="User Name" autoComplete='true' required /><br />
                <input type="email" onChange={changeHandler} name="email" placeholder="Email" autoComplete='true' required /><br />
                <input type="password" onChange={changeHandler} name="password" placeholder="Password" autoComplete='true' required /><br />
                <input type="password" onChange={changeHandler} name="confirmpassword" placeholder="Confirm Password" autoComplete='true' required /><br />
                <input type="submit" value="Register" /><br />
                <div>
                    {errormsg ? <div style={{color:"red"}}>{errormsg}</div> : null}
                </div>
                Already Registered ? <a href='/login'>Login</a>
            </form>
            </center>
        </div>
    )
}

export default Register
